import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";
import {
  Typography,
  MuiThemeProvider,
  IconButton,
  SvgIcon,
  Tooltip
} from "@material-ui/core";
import PortfolioIcon from "@material-ui/icons/ImportContacts";
import Description from "@material-ui/icons/Description";

import { theme } from "./theme";
import GithubIcon from "./github.svg";

function HelloPage() {
  return (
    <div className="App">
      <Typography variant="display4">
        <b>
          <span style={{ color: "blue" }}>Zh</span>erebko
        </b>
        <b>
          <span style={{ color: "blue" }}>D</span>mitry
        </b>
      </Typography>
      <Typography variant="display3" children="Software Developer" />
      <div className="buttons-group">
        <Tooltip title="github" placement="bottom">
          <a href="https://github.com/zhDmitry">
            <img
              src={GithubIcon}
              width={30}
              height={30}
              className="icon-button"
            />
          </a>
        </Tooltip>
        <span className="space" />
        <Tooltip title="portfolio" placement="bottom">
          <a href="https://zhd.js.org/prtfl/">
            <PortfolioIcon style={{ fill: "black", width: 30, height: 30 }} />
          </a>
        </Tooltip>
        <span className="space" />
        <Tooltip title="resume" placement="bottom">
          <a href="https://docs.google.com/document/d/1Phot87i9dKOBq4MeUzTssaNJiMcidDxcOUV3UcO-IO0/edit?usp=sharing">
            <Description style={{ fill: "black", width: 30, height: 30 }} />
          </a>
        </Tooltip>
      </div>
    </div>
  );
}

class App extends Component {
  render() {
    return (
      <MuiThemeProvider theme={theme}>
        <HelloPage />
      </MuiThemeProvider>
    );
  }
}

export default App;



